.App {
  text-align: center;
}

.App-logo {
  /*height: 40vmin;*/
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.container {
  width: 600px;
  border: solid #282c34 2px;
  border-radius: 15px;
  padding: 10px;
  margin: auto;
  margin-left: -150px;
}

.menus {
  border: solid #ffffff 2px;
  border-radius: 10px;
  padding: 5px;
  position: relative;
  margin: auto;
  margin-bottom: 5px;
  width: 250px;
}

.display {
  background-color: #282c34;
  background-image: url('./logo.svg');
  background-repeat: no-repeat;
  background-position: center;
  display: flex;
  flex-wrap: wrap;
  color: aliceblue;
  border-radius: 10px;
  padding: 5px;
}


